:root{
  --color-background: #221407;
  --color-yellow: #f0c600;
  --color-red: #d42c05;
  --color-blue: #1780af;
}
*{
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  list-style-type: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.App{
  width: 100vw;
  background-image: url(./images/Mubleen_website_landscape_background_face_detection_image_afric_6978cfc6-f8e7-476d-bac0-f6b1dd60cb4b.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  color: white;
  z-index: -100;
}
.home{
  min-height: 90vh;
  color: #f0c600;
}
.foot, .nav{
  color: #f0c600;
}
nav ul{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center; 
}
.sign{
  display: flex;
  align-items: center;
  justify-items: center;
  flex-direction: row;
}
.nav-item{
  text-transform: uppercase;
  text-decoration: none;
  color: #f0c600;
  margin-right: 1.2rem;
}
.nav-item:hover{
  color: var(--color-blue);
  transition: .45s all;
}
nav{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem;
}
button.action{
  padding: 6px 16px;
  background-color: #d42c05;
  color: white;
  border-radius: 14px;
  border: none;
}
.home main{
  text-align: center;
  display: grid;
  grid-template-columns: 500px 1fr;
  height: 550px;
  margin-left: 3rem;
}
.intro{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.hero{
  /* font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif; */
  font-family: cursive;
}
.tagline{
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  margin-bottom: 1rem;
}
button{
  cursor: pointer;
}
button:hover{
  background-color: #1780af;
  color: white;
  transition: .6s all;
}
button.button.action .call{
  text-decoration: none;
  padding: .5rem 1.3rem;
  color: white;
}
.intro button.button{
  margin-top: 1rem;
}
div .input{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.home .input div{
  display: flex;
  justify-self: center;
  align-items: center;
  flex-direction: column;
}
.home .input div div{
  display: flex;
  flex-direction: row;
}
.input .url{
  flex: 1;
}
.input #imageUrl{
  min-width: 500px;
  height: 28px;
  border-radius: 50px;
  background-color: var(--color-blue);
  color: white;
}
.input #image-display{
  /* width: 500px; */
  /* min-height: 363px; */
  margin-right: 73px;
  /* max-height: 450px; */
}
.input .position{
  position: relative;
}
.boundingbox{
  position: absolute;
  box-shadow: 0 0 0 3px var(--color-blue) inset;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  cursor: pointer;
  z-index: 5;
}